import 'react-multi-carousel/lib/styles.css';
import colorSharp from "../assets/img/color-sharp.png"

export const Ipssi = () => {
    return (
    <section className="skill" id="ipssi">
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <div className="skill-bx wow zoomIn">
                        <h2>IPSSI</h2>
                        <p>L'IPSSI est une cole d'informatique situe  Saint Quentin en Yvelines. Elle propose des formations en informatique allant de Bac+2  Bac+5. Les domaines de formation comprennent le dveloppement web, la cyberscurit, l'intelligence artificielle, le big data, et bien plus encore.</p>
                        <p>Les cours sont dispenss par des professionnels expriments et des experts du domaine de l'informatique. L'IPSSI dispose galement d'un rseau d'entreprises partenaires pour aider les tudiants  trouver des stages et des opportunits professionnelles.</p>
                        <p>Si vous tes intress par une carrire dans le domaine de l'informatique, l'IPSSI est l'endroit idal pour vous. Pour plus d'informations, veuillez visiter le site web de l'cole : <a href="https://ecole-ipssi.com/ecole-informatique-saint-quentin-en-yvelines/" target="_blank">https://ecole-ipssi.com</a></p>
                    </div>
                </div>
            </div>
        </div>
        <img className="background-image-left" src={colorSharp} alt="Image" />
    </section>
  )
}
